/**
 * GeChiUI dependencies
 */
import { useSelect, useDispatch } from '@gechiui/data';
import {
	BlockSettingsMenuControls,
	store as blockEditorStore,
} from '@gechiui/block-editor';
import { MenuItem } from '@gechiui/components';
import { __ } from '@gechiui/i18n';

export default function ConvertToRegularBlocks( { clientId } ) {
	const { getBlocks } = useSelect( blockEditorStore );
	const { replaceBlocks } = useDispatch( blockEditorStore );

	return (
		<BlockSettingsMenuControls>
			{ ( { onClose } ) => (
				<MenuItem
					onClick={ () => {
						replaceBlocks( clientId, getBlocks( clientId ) );
						onClose();
					} }
				>
					{ __( 'Detach blocks from template part' ) }
				</MenuItem>
			) }
		</BlockSettingsMenuControls>
	);
}
