/**
 * External dependencies
 */
import { kebabCase } from 'lodash';

/**
 * GeChiUI dependencies
 */
import { useDispatch } from '@gechiui/data';
import {
	BlockSettingsMenuControls,
	store as blockEditorStore,
} from '@gechiui/block-editor';
import { MenuItem } from '@gechiui/components';
import { createBlock, serialize } from '@gechiui/blocks';
import { __ } from '@gechiui/i18n';
import { useState } from '@gechiui/element';
import { store as coreStore } from '@gechiui/core-data';
import { store as noticesStore } from '@gechiui/notices';

/**
 * Internal dependencies
 */
import CreateTemplatePartModal from '../create-template-part-modal';

export default function ConvertToTemplatePart( { clientIds, blocks } ) {
	const [ isModalOpen, setIsModalOpen ] = useState( false );
	const { replaceBlocks } = useDispatch( blockEditorStore );
	const { saveEntityRecord } = useDispatch( coreStore );
	const { createSuccessNotice } = useDispatch( noticesStore );

	const onConvert = async ( { title, area } ) => {
		// Currently template parts only allow latin chars.
		// Fallback slug will receive suffix by default.
		const cleanSlug =
			kebabCase( title ).replace( /[^\w-]+/g, '' ) || 'gc-custom-part';

		const templatePart = await saveEntityRecord(
			'postType',
			'gc_template_part',
			{
				slug: cleanSlug,
				title,
				content: serialize( blocks ),
				area,
			}
		);
		replaceBlocks(
			clientIds,
			createBlock( 'core/template-part', {
				slug: templatePart.slug,
				theme: templatePart.theme,
			} )
		);
		createSuccessNotice( __( 'Template part created.' ), {
			type: 'snackbar',
		} );

		// The modal and this component will be unmounted because of `replaceBlocks` above,
		// so no need to call `closeModal` or `onClose`.
	};

	return (
		<>
			<BlockSettingsMenuControls>
				{ () => (
					<MenuItem
						onClick={ () => {
							setIsModalOpen( true );
						} }
					>
						{ __( 'Make template part' ) }
					</MenuItem>
				) }
			</BlockSettingsMenuControls>
			{ isModalOpen && (
				<CreateTemplatePartModal
					closeModal={ () => {
						setIsModalOpen( false );
					} }
					onCreate={ onConvert }
				/>
			) }
		</>
	);
}
