/**
 * External dependencies
 */
import { flow } from 'lodash';

/**
 * GeChiUI dependencies
 */
import { useSelect, useDispatch } from '@gechiui/data';
import { MenuItem } from '@gechiui/components';
import { __ } from '@gechiui/i18n';
import { check } from '@gechiui/icons';
import { speak } from '@gechiui/a11y';

/**
 * Internal dependencies
 */
import { store as editSiteStore } from '../../../store';

export default function FeatureToggle( {
	feature,
	label,
	info,
	messageActivated,
	messageDeactivated,
} ) {
	const speakMessage = () => {
		if ( isActive ) {
			speak( messageDeactivated || __( 'Feature deactivated' ) );
		} else {
			speak( messageActivated || __( 'Feature activated' ) );
		}
	};

	const isActive = useSelect( ( select ) => {
		return select( editSiteStore ).isFeatureActive( feature );
	}, [] );

	const { toggleFeature } = useDispatch( editSiteStore );

	return (
		<MenuItem
			icon={ isActive && check }
			isSelected={ isActive }
			onClick={ flow(
				toggleFeature.bind( null, feature ),
				speakMessage
			) }
			role="menuitemcheckbox"
			info={ info }
		>
			{ label }
		</MenuItem>
	);
}
