/**
 * GeChiUI dependencies
 */
import { __, isRTL } from '@gechiui/i18n';
import { Button } from '@gechiui/components';
import { useSelect, useDispatch } from '@gechiui/data';
import { redo as redoIcon, undo as undoIcon } from '@gechiui/icons';
import { displayShortcut } from '@gechiui/keycodes';
import { store as coreStore } from '@gechiui/core-data';

export default function RedoButton() {
	const hasRedo = useSelect(
		( select ) => select( coreStore ).hasRedo(),
		[]
	);
	const { redo } = useDispatch( coreStore );
	return (
		<Button
			icon={ ! isRTL() ? redoIcon : undoIcon }
			label={ __( 'Redo' ) }
			shortcut={ displayShortcut.primaryShift( 'z' ) }
			// If there are no undo levels we don't want to actually disable this
			// button, because it will remove focus for keyboard users.
			// See: https://github.com/GeChiUI/gutenberg/issues/3486
			aria-disabled={ ! hasRedo }
			onClick={ hasRedo ? redo : undefined }
		/>
	);
}
