/**
 * GeChiUI dependencies
 */
import { __ } from '@gechiui/i18n';
import { TextControl } from '@gechiui/components';
import { useEntityProp } from '@gechiui/core-data';

export default function EditTemplateTitle( { template } ) {
	const [ title, setTitle ] = useEntityProp(
		'postType',
		template.type,
		'title',
		template.id
	);

	return (
		<TextControl
			label={ __( 'Title' ) }
			value={ title }
			help={ __(
				'Give the template a title that indicates its purpose, e.g. "Full Width".'
			) }
			onChange={ ( newTitle ) => {
				setTitle( newTitle || template.slug );
			} }
		/>
	);
}
