/**
 * GeChiUI dependencies
 */
import { useSelect } from '@gechiui/data';
import { store as coreStore } from '@gechiui/core-data';

/**
 * Internal dependencies
 */
import NewTemplate from './new-template';
import NewTemplatePart from './new-template-part';

export default function AddNewTemplate( { templateType = 'gc_template' } ) {
	const postType = useSelect(
		( select ) => select( coreStore ).getPostType( templateType ),
		[ templateType ]
	);

	if ( ! postType ) {
		return null;
	}

	if ( templateType === 'gc_template' ) {
		return <NewTemplate postType={ postType } />;
	} else if ( templateType === 'gc_template_part' ) {
		return <NewTemplatePart postType={ postType } />;
	}

	return null;
}
